import { Routes } from '@angular/router';

export const mobileRoutes: Routes = [
  {
    path: '',
    loadComponent: () => import('./pages/home/home.component').then(m => m.MobileHomeComponent)
  },
  {
    path: 'edit/:id',
    loadComponent: () => import('./pages/editor/editor.component').then(m => m.MobileEditorComponent)
  },
  {
    path: 'settings',
    loadComponent: () => import('./pages/settings/settings.component').then(m => m.MobileSettingsComponent)
  }
];
